<template>
  <strong>
    {{ formatted }}
  </strong>
</template>

<script>

export default {
  name: "TicketTotal",
  components: {
  },
  props: {
    count: {
      type: Number,
      required: true
    },
    price: {
      type: Number,
      required: true
    },
    discountPercent: {
      type: Number,
      required: true
    }
  },
  computed: {
    total() {
      const total = this.count * this.price
      return this.discountPercent
        ? (100 - this.discountPercent) / 100.0 * total
        : total
    },
    formatted() {
      return `$${ Math.round(this.total) / 100}`
    }
  },
  methods: {
  }
};
</script>

