<template>
  <div>
    <b-field
      label="First Name"
      class="column is-8"
      :type="{ 'is-danger': $v.person.firstName.$error }"
      :message="
        $v.person.firstName.$error ? 'Please enter your first name' : ''
      "
    >
    <b-input
        v-model="details.firstName"
        placeholder="Leslie"
        @blur="$v.person.firstName.$touch()"
        @input="onInput"
        type="text"
        icon="account"
        size="is-medium"
      >
      </b-input>
    </b-field>

    <b-field
      label="Email"
      class="column is-8"
      :type="{ 'is-danger': $v.person.email.$error }"
      :message="$v.person.email.$error ? 'Please enter your email' : ''"
    >
      <b-input
        v-model="details.email"
        placeholder="francis@example.com"
        @blur="$v.person.email.$touch()"
        @input="onInput"
        type="email"
        icon="email"
        size="is-medium"
      >
      </b-input>
    </b-field>
  </div>
</template>

<script>

import { mapGetters } from "vuex";

export default {
  name: "PersonDetails",
  components: {
  },
  props: {
    value: {
      type: Object,
      required: true
    },
    $v: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      details: { ...this.value }
    }
  },
  methods: {
    onInput () {
      this.$emit('input', this.details)
    }
  },
};
</script>

