<template>
  <section class="container">
    <page-content :contentId="contentId" :titleColumns="9" :bodyColumns="8" >
      <template slot="beforeContent">
        <p>
          You purchased <strong>{{ purchaseQuantity }}</strong> tickets
          and <strong>{{ allocatedQuantity }}</strong> are allocated.
        </p>
      </template>
    </page-content>

    <upper-nav :ticketOffer="ticketOffer" />

    <div class="ticket-preview column mb-6">
      <h2 class="title is-2">Your Ticket</h2>
      <purchaser-ticket :person="person" />
    </div>

    <div v-if="ticketOffer.ticketCount > 1" class="ticket-preview column is-clearfix">

      <h2 class="title is-2">Shared Tickets</h2>

      <ticket-preview
        v-for="invite in invites"
        :key="invite.id"
        :person="invite"
      />

        <recipient-details-form
          v-if="!allocationReached"
          class="mt-6"
          :allocationReached="allocationReached"
          @add-invite="offerTicket($event)"
        />
    </div>
  </section>
</template>

<script>
import { required, email } from "vuelidate/lib/validators";
import { mapGetters, mapActions } from "vuex";

import PageContent from "@/ticketing/components/PageContent";
import PurchaserTicket from "./PurchaserTicket";
import TicketPreview from "./TicketPreview"
import RecipientDetailsForm from "./RecipientDetailsForm";
import UpperNav from './UpperNav.vue';

const CONTENT_NAME = "invite_page_content"

export default {
  name: "SendTickets",
  components: {
    PageContent,
    PurchaserTicket,
    TicketPreview,
    RecipientDetailsForm,
    UpperNav,
  },
  beforeMount() {
    this.$store.dispatch("layouts/setBannerVisibility", true);
  },
  data() {
    return {
      contentName: CONTENT_NAME,
    };
  },
  validations: {
    person: {
      firstName: { required },
      email: { required, email }
    }
  },
  methods: {
    ...mapActions({
      addInvite: "invites/addInvite"
    }),

    offerTicket(invite) {
      if (this.allocationReached) return

      console.log(invite)

      try {
        this.addInvite(invite)
      } catch (err) {
        this.$snackbar.open({
          message: err,
          type: "is-danger",
          position: "is-bottom-left"
        });
      }
    }
  },
  computed: {
    ...mapGetters({
      event: "events/event",
      ticketOffer: "ticketOffers/ticketOffer",
      content: "content/contentByField",
      invites: "invites/invites",
      allocationReached: "invites/allocationReached",
      person: "people/person",
      purchaseQuantity: "invites/purchaseQuantity",
      allocatedQuantity: "invites/allocatedQuantity"
    }),
    contentId() {
      return this.event.pageContent.find(c => c[0] === this.contentName)[1];
    },
  },
};
</script>

