<template>
    <nav>
      <router-link
      :to="{
          name: 'update-profile',
          params: $route.params
        }"
        class="button is-primary is-large">
        Update Profile
      </router-link>
      <a :href="receiptUrl"
        v-if="!ticketOffer.invitePaidFor"
        target="_blank"
        class="button is-secondary is-large"
        rel="noopener noreferrer">
        GST Receipt
      </a>
    </nav>
</template>

<script>

export default {
  name: "UpperNav",
  props: {
    ticketOffer: {
      type: Object,
      required: true
    }
  },
  computed: {
    receiptUrl() {
      const baseURL = process.env.VUE_APP_PDF_BASE_URL
      return `${baseURL}/receipts/${this.ticketOffer.id}`
    }
  }
};
</script>

<style scoped>
nav {
  width: 100%;
  display: flex;
  padding: 1rem 2rem 2rem 2rem;
}
