<template>
  <section class="container">
    <upper-nav v-if="!ticketOffer.invitedBy" />

    <section v-if="needsDetails" class="section">
      <div class="content has-text-centered">
        <p>Please fill in your details to get your ticket</p>
      </div>
    </section>

    <page-content :contentId="contentId">
      <template v-if="ticketOffer.invitePaidFor" slot="beforeContent">
        <p>
          Hi {{ personName }}, {{ invitedByName }} has bought you a ticket to
          {{ event.title }}, we just need a few details before you can claim
          your ticket.
        </p>
        <p>Please fill in the form below</p>
      </template>
      <template v-else slot="beforeContent">
        <p>
          Hi <strong>{{ personName }}</strong>, Please update your details below to receive your ticket for
          <strong>
          {{ event.title }}
          </strong>.
        </p>
      </template>
    </page-content>

    <ticket-offer-form @submitForm="proceedToTicket" submitText="update profile" />
  </section>
</template>

<script>
import PageContent from "@/ticketing/components/PageContent";
import TicketOfferForm from "@/ticketing/views/TicketOfferForm";
import ProfileService from "@/services/ProfileService";
import NProgress from "nprogress";
import UpperNav from "./UpperNav";
import { personNameMixin } from "@/mixins/personNameMixin";
import { markdownSubstitutionsMixin } from "@/mixins/markdownSubstitutionsMixin";
import { mapGetters } from "vuex";

export default {
  name: "Profile",
  components: {
    PageContent,
    TicketOfferForm,
    UpperNav,
  },
  beforeMount() {
    this.$store.dispatch("layouts/setBannerVisibility", true);
  },
  mixins: [personNameMixin, markdownSubstitutionsMixin],
  props: {
    needsDetails: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      contentName: "ticket_offer_page_content"
    };
  },
  computed: {
    ...mapGetters({
      event: "events/event",
      person: "people/person",
      ticketOffer: "ticketOffers/ticketOffer"
    }),
    contentId() {
      return this.event.pageContent.find(c => c[0] === this.contentName)[1];
    },

    invitedByName() {
      const { firstName, lastName } = this.ticketOffer.invitedBy
      return lastName
        ? `${ firstName } ${lastName}`
        : firstName
    },
    redirectedFromTicket() {
      return this.$router
    },
    applicationQuestions() {
      return this.person.data;
    },
    ticketQuestions() {
      return this.person.data;
    }
  },

  methods: {
    proceedToTicket() {
      ProfileService.updateProfile(this.person.id, {
        person: {
          firstName: this.person.firstName,
          lastName: this.person.lastName,
          email: this.person.email
        },
        profile: {
          ...this.applicationQuestions,
          ...this.ticketQuestions
        }
      })
        .then(() => {
          this.$emit("recalcNeeded");
          NProgress.done();
          this.$router.push({
            name: 'ticket',
            params: this.$route.params }
          )
        })
        .catch(e => {
          NProgress.done();
          console.log("ERROR", e);
        });
    }
  }
};
</script>

<style>
.link {
  text-decoration: underline;
}
</style>
